h1,
h2,
h3 {
  margin: 0;
}

.page {
  font-family: "Cinzel", serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 27px;
  color: $colorBlack;
  scroll-behavior: smooth;
  
  &__body {
    margin: 0;

    &--with-menu {
      overflow-y: hidden;
    }
  }

  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    transform: translateX(-100%);
    transition: transform 0.3s;
     
    &:target {
      transform: translateX(0);
    }
  }

  &__section {
    margin-bottom: 100px;

    @include onTablet {
      margin-bottom: 140px;
    }

    &-title {
      margin-bottom: 48px;
      font-weight: 700;
      font-size: 32px;
      line-height: 32px;
      text-align: center;
      letter-spacing: -1.5px;
      color: $colorBlack;

      @include onTablet {
        font-size: 48px;
        line-height: 48px;
      }
    }
  }
}
